/* Shades */
/* Colors */
html,
body,
ul,
li,
h1,
p {
  border: none;
  margin: 0;
  padding: 0;
}

body {
  background: #101112;
}

main {
  width: 80%;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}

h1,
p {
  color: #BCC5D1;
  font-family: Arial;
  text-align: center;
}

h1 {
  margin: 0 0 10px;
}

p {
  line-height: 1.2;
}

ul {
  list-style: none;
  margin: 10px 0;
  text-align: center;
}

li {
  display: inline;
  margin: 0 5px;
}

a {
  color: #7495B6;
  text-decoration: none;
  transition: .2s;
}
a:hover {
  color: #586270;
}

/* Mixin for Shades/Colors */
/* Blocks {{{ */
.shades,
.colors {
  display: block;
  width: 336px;
}

.shades {
  margin: 20px auto 5px auto;
}

.colors {
  margin: 0 auto;
}

